:host {
  width: 100%;
  height: 1.5rem;
  display: block;
  overflow: hidden;
  position: relative; }
  :host .slide-wrap {
    height: 100%;
    display: flex;
    flex-direction: row;
    padding: 0;
    margin: 0;
    position: absolute; }
    :host .slide-wrap .slide {
      height: 100%;
      flex: 1;
      transition: transform .5s; }
      :host .slide-wrap .slide > * {
        width: 100%;
        height: 100%;
        overflow: hidden; }
      :host .slide-wrap .slide img {
        width: 100%;
        height: 100%; }
  :host .swipe-pagination {
    position: absolute;
    bottom: 5px;
    left: 0;
    text-align: center;
    width: 100%; }
  :host .swipe-pagination-bullet {
    width: 8px;
    height: 8px;
    display: inline-block;
    -webkit-border-radius: 100%;
    border-radius: 100%;
    background: #fff;
    border: 1px #c7c3bf solid;
    margin-right: 5px; }
  :host .active {
    background: #ef7f42;
    border-color: #ef7f42; }
  :host .lazy {
    visibility: visible; }
